@import "_variables.scss";

.appbar {
  width: 100%;
  height: 65px;
  line-height: 62px;
  overflow: hidden;
  color: #000;
  &.fixed,
  &.dense,
  &.short,
  &.collapse,
  &.collapsed {
    position: sticky;
    top: 0px;
    z-index: 2;
  }
  &.dense {
    transition: top 0.4s;
  }
  &.collapse {
    transition: width 0.4s;
  }
  &.collapsed {
    width: 115px;
    border-radius: 0px 0px 30px 0px;
    .title {
      display: none;
    }
  }
  > .wrap {
    width: 95%;
    height: 100%;
    position: relative;
    margin: 0px auto;
    display: flex;
    justify-content: space-between;
    > section {
      .search {
        width: auto;
        height: 35px;
        overflow: hidden;
        display: inline-block;
        background-color: rgba(255, 255, 255, 0.15);
        border-radius: 5px;
        color: #fff;
        vertical-align: middle;
        position: relative;
        &:hover {
          background-color: rgba(255, 255, 255, 0.21);
        }
        > i {
          font-size: 22px;
          text-indent: 20px;
          line-height: 30px;
          position: absolute;
          top: 4px;
        }
        > input {
          width: 60px;
          height: 100%;
          padding: 0;
          border: none;
          float: right;
          background: transparent;
          transition: width 0.5s;
          padding-right: 8px;
          padding-left: 50px;
          color: inherit;
          &:focus {
            width: 200px;
          }
          &:hover,
          &:focus {
            outline: none;
          }
          &::placeholder {
            color: inherit;
          }
        }
      }

      > img {
        height: 40px;
        vertical-align: middle;
        padding: 8px;
      }
      > a {
        min-width: 50px;
        height: 40px;
        line-height: 40px;
        padding: 0px 6px 0px 6px;
        display: inline-block;
        text-decoration: none;
        text-transform: uppercase;
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        font-family: $font;
        cursor: pointer;
        vertical-align: middle;
        &.icon {
          text-transform: none;
          width: 50px;
          height: 50px;
          padding: 0;
          margin: 0;
          text-align: center;
          border-radius: 50%;
          > i {
            font-size: 24px;
            position: relative;
            top: 0.5em;
          }
          > svg {
            width: 24px;
            height: 24px;
            position: relative;
            top: 12px;
          }
          > img {
            margin-top: 8px;
          }
        }
      }
      > .title {
        color: inherit;
        vertical-align: middle;
        font-family: $font;
        letter-spacing: 0.15px;
        font-weight: 500;
        font-size: 20px;
        text-transform: capitalize;
      }
    }
    > .left {
      float: left;
      height: 100%;
    }
    > .center {
      margin: 0px auto;
      height: 100%;
      &.static {
        position: absolute;
        left: 50%;
        margin-left: -50px;
        & ~ .right {
          margin-left: auto;
          margin-right: 0;
        }
      }
    }
    > .right {
      height: 100%;
      float: right;
    }
  }
}

.badge[data-badge]{
  position: relative;
  &:after{
    content: attr(data-badge);
    width: 22px;
