@import "_mixins.scss";
@import "_variables.scss";

*:hover,
*:focus {
  outline: none;
}

$primary_color: color(indigo);

.text-field {
  display: flex;
  flex-direction: column;
  position: relative;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  > i {
    color: #707071;
    pointer-events: none;
  }
  > .line-ripple {
    width: 100%;
    height: 2px;
    background: $primary_color;
    padding: 0;
    position: absolute;
    top: 55px;
    left: 0;
    transform: scaleX(0);
    transition: transform 0.18s cubic-bezier(0.4, 0, 0.2, 1);
  }
  > input,
  textarea {
    width: 100%;
    height: 56px;
    border: none;
    border-bottom: 2px solid #8e8e8f;
    padding: 20px 0px 4px 2px;
    vertical-align: center;
    border-radius: 4px 4px 0 0;
    outline: none;
    font-size: 16px;
    box-sizing: border-box;
    transition: background 0.5s;
    background: transparent;
      padding-left: 0px;
      &:hover, &:focus {
        background: transparent;
      }

    &:focus {
      border-bottom: none;
      & ~ .floating-label {
        color: $primary_color;
      }
      & ~ .line-ripple {
        transform: scaleX(1);
      }
    }
    &:focus,
    &:not(:placeholder-shown) {
      & ~ .floating-label {
        top: 8px;
        font-size: 12px;
        will-change: top, font-size, color;
      }
    }
  }
  > .floating-label {
    font-size: 16px;
    position: absolute;
    top: 16px;
    left: 0px;
    pointer-events: none;
    color: rgba(0, 0, 0, .54);
    transition: top 0.18s cubic-bezier(0.4, 0, 0.2, 1),
      font-size 0.18s cubic-bezier(0.4, 0, 0.2, 1);
    clear: both;
    &:first-letter {
      text-transform: capitalize;
    }
  }
  > .helper {
    font-size: 12px;
    height: 16px;
    color: #666666;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: absolute;
    bottom: -20px;
    left: 0px;
  }

  &.fill{
    > .floating-label {
      top: 16px;
      left: 12px;
    }
    > input{
      padding: 20px 12px 4px 12px;
       &:hover {
          background: #e5e5e5;
       }
      &:focus{
        background: #e5e5e5;
      }
    }
  }
  &.outlined {
    > .floating-label {
      transition: all 0.1s;
    }
    > input, textarea {
      padding: 20px 26px 12px 12px;
      border: none;
      height: 56px;
      border: 1.8px solid #c2c2c2;
      font-size: 16px;
      border-radius: 6px;
    }
    > input {
      &:hover {
        background: transparent;
      }
      &:focus {
        background: transparent;
        border: 2px solid $primary_color;
      }
      &:focus,
      &:not(:placeholder-shown) {
        & ~ .floating-label {
          top: -10px;
          left: 10px;
          font-size: 12px;
          will-change: top, font-size, color;
          background: #fff;
          padding: 4px;
        }
      }
    }
    > .line-ripple {
      display: none;
    }
    &.capsule {
      >.floating-label {
        left: 20px;
      }
      .lead {
        & ~.floating-label {
          left: 45px;
        }
      }
      >input {
        padding: 12px 15px;
        border-radius: 28px;
        &:focus,
        &:not(:placeholder-shown) {
          &~.floating-label {
            left: 25px;
          }
        }
      }
    }
  }
  &.fill {
    > input,
    textarea {
      background: #f5f5f6;
    }
  }
  .lead {
    position: absolute;
    top: 17px;
    left: 15px;
    & ~ input, & ~ textarea {
      padding-left: 45px !important;
    }
    & ~ .floating-label {
      left: 45px;
    }
    & ~ .helper{
      left: 20px;
    }
  }
  .trail {
    position: absolute;
    top: 18px;
    right: 15px;
    & ~ input{
      padding-left: 8px;
      padding-right: 45px !important;
    }
    & ~ .floating-label {
      left: 8px;
    }
    & ~ .helper{
      left: 10px;
    }
  }
}

