@import "_mixins.scss";
@import "_variables.scss";

$small-loader-width: 60px;
$small-loader-height: 60px;

$lin-small-width: 200px;

$large-loader-width: 250px;
$large-loader-height: 100px;

$base-loader-width: 150px;
$base-loader-height: 60px;

$loader-margin: 2em;

.loader {
  background: #fff;
  line-height: normal;
  position: fixed;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 5px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.1), 0 5px 15px 0 rgba(0, 0, 0, 0.1);
  pointer-events: auto !important;
  visibility: hidden;
  overflow: hidden;
  > .circular {
    width: 40px;
    height: 40px;
  }
  > .linear {
    width: calc(100% - 10%);
    height: 4px;
  }
  &.small {
    width: $small-loader-width;
    height: $small-loader-height;
    display: flex;
    align-items: center;
    justify-content: center;
    &.lin {
      width: $lin-small-width;
      > .linear {
        margin-top: 4px;
      }
    }
  }
  &.large {
    width: $large-loader-width;
    height: $large-loader-height;
    font-family: $font;
    > .title {
      font-size: 16px;
      width: 100%;
      display: block;
      padding: 16px;
      box-sizing: border-box;
    }
    > .subtext {
      width: calc(100% - 60px);
      position: absolute;
      font-size: 15px;
      left: 60px;
      margin-top: 8px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    > .circular {
      width: 35px;
      height: 35px;
      float: left;
      margin-left: 15px;
    }

    &.lin {
      > .linear {
        width: 90%;
        margin-left: 5%;
      }
    }
  }
  &.base{
    width: $base-loader-width;
    height: $base-loader-height;
    font-family: $font;
    display: flex;
    align-items: center;
    justify-content: center;       
    > .title {
      width: calc(100% - 60px);
      position: absolute;
      font-size: 15px;
      left: 50px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    > .circular { 
      width: 35px;
      height: 35px;
      position: absolute;
      left: 10px;
    }
  }
  &.trans {
    background: transparent;
    box-shadow: none;
  }
  
  &.dark{
    background: #222;
    color: #fff
  }

  &.left {
    left: $loader-margin;
    right: auto;
  }
  &.right {
    right: $loader-margin;
    left: auto;
  }
  &.top {
    top: $loader-margin;
    bottom: auto;
  }
  &.bottom {
    bottom: $loader-margin;
    top: auto;
  }
  @media screen and (max-width: 400px) {
    &.left,
    &.right {
      margin: 0px auto;
      right: 0px !important;
      left: 0px !important;
    }
  }
  &.slideDownIn {
    visibility: visible !important;
    @include animation(slideDownIn 1s ease);
  }
}

@include keyframes(slideDownIn) {
  0% {
    transform: translateY(-10%);
  }
  100% {
    transform: translateY(0%);
  }
}

