$not-active: rgba(color(black, base), 0.4);
$primary-color: color(cyan, base);
$font: "Roboto", "Segoe UI", BlinkMacSystemFont, system-ui, -apple-system;

.radio {
  z-index: 0;
  position: relative;
  display: inline-block;
  color: rgba(color(black, base), 0.87);
  font-family: $font;
  font-size: 16px;
  line-height: 1.5;
  > input {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    z-index: -1;
    position: absolute;
    left: -1px;
    top: -8px;
    display: block;
    margin: 0;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: $not-active;
    outline: none;
    opacity: 0;
    transform: scale(1);
    pointer-events: none;
    transition: opacity 0.3s, transform 0.2s;
  }
  > span {
    display: inline-block;
    width: 100%;
    cursor: pointer;
    padding-left: 10px;
    &::before {
      content: "";
      display: inline-block;
      box-sizing: border-box;
      margin: 2px 10px 2px 0;
      border: solid 2px;
      border-color: $not-active;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      vertical-align: top;
      transition: border-color 0.2s;
    }
    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 2px;
      left: 10px;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      background-color: $primary-color;
      transform: translate(5px, 5px) scale(0);
      transition: transform 0.2s;
    }
  }
  > input {
    &:checked {
      background-color: $primary-color;
      & + span::before {
        border-color: $primary-color;
      }
      & + span::after {
        transform: translate(5px, 5px) scale(1);
      }
    }
    &:focus {
      opacity: 0.12;
    }
  }
  &:hover {
    > input {
      opacity: 0.04;
      &:focus {
        opacity: 0.16;
      }
    }
  }
  /*Active*/
  > input {
    &:active {
      opacity: 1;
      transform: scale(0);
      transition: transform 0s, opacity 0s;
      & + span::before {
        border-color: $primary-color;
      }
    }
    &:disabled {
      opacity: 0;
      & + span {
        color: rgba(0, 0, 0, 0.38);
        cursor: initial;
        &::before {
          border-color: currentColor;
        }
        &::after {
          background-color: currentColor;
        }
      }
    }
  }
  &.txt-left{
    padding-right: 0px;
    margin-right: 10px;
    > input{
      left: calc(100% - 30px);
      position: absolute;
    }
    > span{
      &::after{
        left: auto;
        right: 10px;
      }
      &::before{
        float: right;
        position: relative;
        right: 0px;
      }
    }
  }
}

