    &:last-child {
      position: static;
      margin: 0;
    }
    &:focus {
      outline: none;
    }
    &:disabled {
      cursor: default;
      opacity: 0.38;
      & + span {
        color: rgba(0, 0, 0, 0.38);
      }
    }

    &::-webkit-slider-runnable-track {
      margin: 17px 0;
      border-radius: 1px;
      width: 100%;
      height: 2px;
      background-color: rgba($main-color, 0.24);
    }
    &::-webkit-slider-thumb {
      appearance: none;
      -webkit-appearance: none;
      border: none;
      border-radius: 50%;
      height: 2px;
      width: 2px;
      transform: scale(6, 6);
      transition: box-shadow 0.2s;
